import React, { Component } from 'react';
import './Contact.css';
import gif2 from './gif2.gif';
import Responsive from 'react-responsive';
const Desktop = props => <Responsive {...props} minWidth={992} />;
const NotDesktop = props => <Responsive {...props} maxWidth={992} />;

class Contact extends Component {
    render() {
        return (
            <div class="unskew-left pb-5">
                <h1 class="h1-responsive heading font-weight-bold text-center py-5"><span class="redtext">&nbsp;Contact / Follow Us&nbsp;</span></h1>
                <h2 class="p-3 team-links">
                    <a href="mailto:help@ucltechsoc.com" class="redtext p-2 m-3" title="Email Us">
                        <i class="fa fa-envelope"></i>
                    </a>

                    <a href="https://facebook.com/ucltechsoc" class="redtext p-2 mx-3" title="UCL Technology Society on Facebook">
                        <i class="fa fa-facebook-official"></i>
                    </a>

                    <a href="https://twitter.com/ucltechsoc" class="redtext p-2 mx-3" title="UCL Technology Society on Twitter">
                        <i class="fa fa-twitter"></i>
                    </a>
                </h2>

                <Desktop>
                <h1 class="h1-responsive heading font-weight-bold text-center my-5"><img src={gif2} class="gif-2"/><span class="redtext">&nbsp;Frequently Asked Questions [FAQs]&nbsp;</span> <img src={gif2} class="gif-2"/></h1>
                </Desktop>
                <NotDesktop>
                <h1 class="h1-responsive heading font-weight-bold text-center my-5"><span class="redtext">&nbsp;Frequently Asked Questions [FAQs]&nbsp;</span> </h1>
                </NotDesktop>
                <div class="text-justify w-responsive mx-auto p-3 about-text">
                    <div class="divider px-3">
                        <div class="row justify-content-center">
                            <div class="col-12 p-3 m-2 card-about linesAround">
                                <h3 class="subheading text-center"> Who can attend Porticode? </h3>
                                <h6 class="px-4"> Porticode is open to students (from 18 year olds to post-graduates) and those that have graduated from university in the past twelve months. In order to participate, you need to follow the <a href="http://static.mlh.io/docs/mlh-code-of-conduct.pdf">MLH Code Of Conduct</a>. </h6>
                            </div>
                            <div class="col-12 p-3 m-2 card-about linesAround">
                            <h3 class="subheading text-center"> I don't know how to code... Can I attend? </h3>
                                <h6 class="px-4"> Of course! There's need for various roles in a successful team, like a graphics designer. Simply having an idea entitles you to participate in Porticode. There are also workshops and mentoring for those keen to learn about developing software. </h6>
                            </div>
                            <div class="col-12 p-3 m-2 card-about linesAround">
                            <h3 class="subheading text-center"> How much does a ticket cost? </h3>
                                <h6 class="px-4"> It's absolutely free! The weekend will have free food, beverages and entertainment... We owe it all to our generous sponsors. </h6>
                            </div>
                            <div class="col-12 p-3 m-2 card-about linesAround">
                            <h3 class="subheading text-center"> What should I bring? </h3>
                                <h6 class="px-4"> Bring your Ticket, Student ID, a valid Proof of Age, Laptop, chargers, and any other hardware that you plan on using. Its advised to bring a change of spare clothes, a toothbrush, and any other toiletries you may need. You may also bring a sleeping bag. </h6>
                            </div>
                            <div class="col-12 p-3 m-2 card-about linesAround">
                            <h3 class="subheading text-center"> What if I don't have a team? </h3>
                                <h6 class="px-4"> Team formation will be held at the beginning of the hackathon so that no one hacks alone, unless they really want to. [There is a reason why The Avengers performed a lot better than Man of Steel]. The suggested team size is 3.</h6>
                            </div>
                            <div class="col-12 p-3 m-2 card-about linesAround">
                            <h3 class="subheading text-center"> Are there travel reimbursements? </h3>
                                <h6 class="px-4"> Unfortunately, we aren't reimbursing travel costs this year.</h6>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
        );
    }
}

export default Contact;


