import React, { Component } from 'react';
import './App.css';
import 'font-awesome/css/font-awesome.min.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'mdbreact/dist/css/mdb.css';
import porticode_logo from './logo_new.png';
import background_logo from './background.jpg';
import Responsive from 'react-responsive';
import Timeline from "./Timeline";
import PorticodeCover from './porticodecover.png';
import gif1 from './gif1.gif';
import spideygif from './spideygif.gif';
import About from './About.js';
import Sponsors from './Sponsors.js';
import Team from './Team.js';
import Contact from './Contact.js';
const Desktop = props => <Responsive {...props} minWidth={992} />;
const NotDesktop = props => <Responsive {...props} maxWidth={992} />;


class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <div class="container white-border py-4 px-3">
            <Desktop>
              <img src={porticode_logo} class="img-fluid porticode_logo" alt="responsive image" />
            </Desktop>
            <NotDesktop>
              <img src={porticode_logo} class="img-fluid porticode_logo" alt="responsive image" />
            </NotDesktop>
            <h1 class="h1-responsive heading">17th - 18th November 2018</h1>
            <h1 class="h1-responsive heading"><span class="redtext">&nbsp;University College London&nbsp;</span></h1>
          </div>
          <div class="my-5">
          <h3 class="h3-responsive subheading py-4"><a href="https://www.eventbrite.com/e/ucl-porticode-30-tickets-50786337302" class="white-text"><span class="linesAround">GET YOUR <span class="red-text">TICKETS</span> HERE!</span></a></h3>
          <h4 class="subheading"><a href="https://www.facebook.com/porticode/" class="white-text">FOLLOW US ON <span class="red-text">FACEBOOK</span> TO STAY UPDATED!</a></h4>
          </div>
        </header>

        <section class="parallax1 py-5 px-5">
          <About />
        </section>

        <div class="py-4 skew-left card">
          <h1 class="h1-responsive heading px-2">Did we mention our theme this year is Superheroes?</h1>
          <div class="container">
            <div class="row">
              <div class="col col-lg-6 col-sm-12 align-self-center">
                <img src={gif1} className="gif" />
              </div>
              <div class="col col-lg-6 col-sm-12 align-self-center unskew-left">
                <h1 class="h1-responsive heading px-2"> SO <span class="redtext">&nbsp;SUIT UP!&nbsp;</span></h1>
                <h6 class="text-justify w-responsive mx-auto my-5 card border-dark p-4">
                  Not all heroes wear capes... Some of them learn to code for the first time, some teach someone else how to code, others just hog free food while playing Fortnite all weekend.
                  <br /><br />
                  To celebrate and promote this heroism, this year we invite anyone and everyone remotely interested in technology - as long as you intend to learn something new.
                </h6>
              </div>
            </div>
          </div>
          <h4 class="text-center h4-responsive mx-auto my-3 py-3 px-3 heading">
            Oh yeah, we also want you to get your Fandom on! Whether you're into a Cinematic Universe, an Anime, Comics, Game-verse or something we missed out, wear it with pride and get a chance to Win prizes!
          </h4>
        </div>

        <section class="parallax2 skew-left pb-5">
          <div class="unskew-left pb-5 px-auto">
            <Sponsors />
          </div>
        </section>

        <section class="map scroll-right">
          <img src={spideygif} class="inner" />
          <div class="">
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2482.3883975192102!2d-0.13613222497559857!3d51.52443568720378!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x48761b2f69173579%3A0xd008c67faecc133e!2sUniversity+College+London!5e0!3m2!1sen!2suk!4v1539074467068" width="100%" height="600" frameborder="1" allowfullscreen />
          </div>
        </section>

        <section class="parallax5 py-5">
          <Timeline />
        </section>

        <section class="parallax3">
          <Team />
        </section>

        <section class="parallax4 py-5 skew-left">
          <Contact />
        </section>

        <section class="white-text about-text">
          <div class="container">
            <div class="row">
              <div class="col align-self-center">
                <img src={porticode_logo} class="img-fluid porticode_logo"/>
              </div>
              <div class="col align-self-center">
              <Desktop>
                <h4 class="h4-responsive subheading">Presented by <a href="https://www.ucltechsoc.com" class="linesAround redtext">UCL Technology Society</a></h4>
                </Desktop>
                <NotDesktop>
                <p class="w-responsive subheading">Presented by <br/><a href="https://www.ucltechsoc.com" class="linesAround redtext">UCL Technology Society</a></p>
                  </NotDesktop>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default App;



